/*
* @Cache: 
* :
* 1.GridData: json [GM]
* 2.Cache:  [DOM]
* 3.UserMemory:  [localStorage]
* */
import { $, Base } from './Base';
import store from './Store';
class Cache {
	constructor() {
		this.initCoreMethod();
		this.initGridData();
		this.initUserMemory();
	}
	/**
	 * json trcache-key
	 */
	initGridData() {
		/**
		 * 
		 * @param $table grid,
		 * @param target tr[Element or NodeList]
		 * @returns {*}
         * @private
         */
		this.__getRowData = ($table, target) => {
			const gmName = Base.getKey($table);
			if (!store.responseData[gmName]) {
				return;
			}
			// target type = Element , ;
			if ($.type(target) === 'element') {
				return store.responseData[gmName][target.getAttribute('cache-key')];
			} else if ($.type(target) === 'nodeList') {
				// target type =  NodeList , 
				let rodData = [];
				$.each(target, function (i, v) {
					rodData.push(store.responseData[gmName][v.getAttribute('cache-key')]);
				});
				return rodData;
			} else {
				// Element NodeList, 
				return {};
			}
		};

		/**
		 * 
		 * @param gmName
		 * @param key
         * @param value
         */
		this.setRowData = (gmName, key, value) => {
			if (!store.responseData[gmName]) {
				store.responseData[gmName] = {};
			}
			store.responseData[gmName][key] = value;
		};

		/**
		 * 
		 * @param $table
         */
		this.getTableData = $table => {
			return store.responseData[Base.getKey($table)] || {};
		};
	}

	// TODO : , .
	/**
	 * 
	 */
	initUserMemory() {

		/**
		 * 
		 * @param $table
		 * @returns {boolean}
         */
		this.delUserMemory = $table => {
			// table, 
			if (!$table || $table.length === 0) {
				window.localStorage.removeItem('GridManagerMemory');
				return true;
			}
			let GridManagerMemory = window.localStorage.getItem('GridManagerMemory');
			if (!GridManagerMemory) {
				return false;
			}
			GridManagerMemory = JSON.parse(GridManagerMemory);

			// table, 
			const _key = this.getMemoryKey($table);
			delete GridManagerMemory[_key];

			// , 
			window.localStorage.setItem('GridManagerMemory', JSON.stringify(GridManagerMemory));
			return true;
		};

		/**
		 * 
		 * @param $table
		 * @returns {*}
		 */
		this.getMemoryKey = $table => {
			const settings = this.getSettings($table);
			// table
			if (!$table || $table.length === 0) {
				Base.outLog('getUserMemory:table', 'error');
				return false;
			}
			//   
			const noCache = $table.attr('no-cache');
			if (noCache && noCache === 'true') {
				Base.outLog('html[grid-managerth-name]', 'info');
				return false;
			}
			if (!window.localStorage) {
				Base.outLog('localStorage', 'info');
				return false;
			}
			return window.location.pathname + window.location.hash + '-' + settings.gridManagerName;
		};

		/**
		 * 
		 * @param $table
		 * @returns {*} ,
		 */
		this.getUserMemory = $table => {
			if (!$table || $table.length === 0) {
				return {};
			}
			const _key = this.getMemoryKey($table);
			if (!_key) {
				return {};
			}
			let GridManagerMemory = window.localStorage.getItem('GridManagerMemory');
			// grid-manager-cache-error
			if (!GridManagerMemory || GridManagerMemory === '{}') {
				$table.attr('grid-manager-cache-error', 'error');
				return {};
			}
			GridManagerMemory = JSON.parse(GridManagerMemory);
			const _data = {
				key: _key,
				cache: JSON.parse(GridManagerMemory[_key] || '{}')
			};
			return _data;
		};

		/**
		 * 
		 * @param $table
		 * @returns {boolean}
		 */
		this.saveUserMemory = $table => {
			const Settings = this.getSettings($table);
			const _this = this;

			// 
			if (Settings.disableCache) {
				return false;
			}
			//   
			const noCache = $table.attr('no-cache');
			if (!$table || $table.length === 0) {
				Base.outLog('saveUserMemory:table', 'error');
				return false;
			}
			if (noCache && noCache === 'true') {
				Base.outLog('', 'info');
				return false;
			}
			if (!window.localStorage) {
				Base.outLog('localStorage', 'error');
				return false;
			}
			const thList = $('thead[grid-manager-thead] th', $table);
			if (!thList || thList.length === 0) {
				Base.outLog('saveUserMemory:thList,table,thead,th', 'error');
				return false;
			}

			let _cache = {};
			let _pageCache = {};
			let _thCache = [];
			let _thData = {};

			let $v;
			$.each(thList, (i, v) => {
				$v = $(v);
				_thData = {};
				_thData.th_name = $v.attr('th-name');
				if (Settings.supportDrag) {
					_thData.th_index = $v.index();
				}
				if (Settings.supportAdjust) {
					// 
					_thData.th_width = $v.width();
				}
				if (Settings.supportConfig) {
					_thData.isShow = $(`.config-area li[th-name="${_thData.th_name}"]`, $table.closest('.table-wrap')).find('input[type="checkbox"]').get(0).checked;
				}
				_thCache.push(_thData);
			});
			_cache.th = _thCache;

			// 
			if (Settings.supportAjaxPage) {
				_pageCache.pSize = parseInt($('select[name="pSizeArea"]', $table.closest('.table-wrap')).val(), 10);
				_cache.page = _pageCache;
			}
			const _cacheString = JSON.stringify(_cache);
			let GridManagerMemory = window.localStorage.getItem('GridManagerMemory');
			if (!GridManagerMemory) {
				GridManagerMemory = {};
			} else {
				GridManagerMemory = JSON.parse(GridManagerMemory);
			}
			GridManagerMemory[_this.getMemoryKey($table)] = _cacheString;
			window.localStorage.setItem('GridManagerMemory', JSON.stringify(GridManagerMemory));
			return _cacheString;
		};
	}

	/**
	 * 
	 */
	initCoreMethod() {
		/**
		 * 
		 * @param $table
		 * @returns {*}
		 */
		this.getSettings = $table => {
			if (!$table || $table.length === 0) {
				return {};
			}
			// clone 
			// return $.extend(true, {}, $table.data('settings'));
			return $.extend(true, {}, store.settings[Base.getKey($table)] || {});
		};

		/**
		 * 
		 * @param $table
		 * @param settings
		 */
		this.setSettings = ($table, settings) => {
			// const data = $.extend(true, {}, settings);
			// $table.data('settings', data);
			store.settings[Base.getKey($table)] = $.extend(true, {}, settings);
		};

		/**
		 * 
		 * @param $table
		 * @param version 
		 */
		this.cleanTableCacheForVersion = () => {
			const cacheVersion = window.localStorage.getItem('GridManagerVersion');
			// 
			if (!cacheVersion) {
				window.localStorage.setItem('GridManagerVersion', store.version);
			}
			// , 
			if (cacheVersion && cacheVersion !== store.version) {
				this.cleanTableCache(null, ',');
				window.localStorage.setItem('GridManagerVersion', store.version);
			}
		};

		/**
		 * 
		 * @param $table
		 * @param cleanText
		 */
		this.cleanTableCache = ($table, cleanText) => {
			// table, 
			if ($table === null) {
				this.delUserMemory();
				Base.outLog(`,${cleanText}`, 'info');
				// table, 
			} else {
				const Settings = this.getSettings($table);
				this.delUserMemory($table);
				Base.outLog(`${Settings.gridManagerName},${cleanText}`, 'info');
			}
		};


		/**
		 * thead: , , 
		 * @param $table
		 */
		this.configTheadForCache = $table => {
			let Settings = this.getSettings($table);
			const _this = this;
			// 
			const _data = _this.getUserMemory($table);
			const _domArray = [];

			// $table 
			if (!_data || $.isEmptyObject(_data) || !_data.cache || $.isEmptyObject(_data.cache)) {
				return;
			}

			// 
			let _cache = _data.cache;

			// th 
			let _thCache = _cache.th;

			// 
			let _thNameTmpList = [];
			let _dataAvailable = true;

			// th
			let _th;

			// thjson
			let _thJson;

			// 
			if (!_thCache || _thCache.length !== $('thead th', $table).length) {
				_this.cleanTableCache($table, '');
				return;
			}
			$.each(_thCache, (i2, v2) => {
				_thJson = v2;
				_th = $('th[th-name=' + _thJson.th_name + ']', $table);
				if (_th.length === 0 || _thNameTmpList.indexOf(_thJson.th_name) !== -1) {
					_this.cleanTableCache($table, '');
					_dataAvailable = false;
					return false;
				}
				_thNameTmpList.push(_thJson.th_name);
			});

			// 
			if (_dataAvailable) {
				$.each(_thCache, (i2, v2) => {
					_thJson = v2;
					_th = $('th[th-name=' + _thJson.th_name + ']', $table);
					// 
					if (Settings.supportAdjust && _th.attr('gm-create') !== 'true') {
						_th.css('width', _thJson.th_width);
					}
					// 
					if (Settings.supportDrag && typeof _thJson.th_index !== 'undefined') {
						_domArray[_thJson.th_index] = _th;
					} else {
						_domArray[i2] = _th;
					}
					// 
					if (Settings.supportConfig) {
						Base.setAreVisible(_th, typeof _thJson.isShow === 'undefined' ? true : _thJson.isShow, true);
					}
				});

				// 
				if (Settings.supportDrag) {
					$table.find('thead tr').html(_domArray);
				}
			}
		};

		/**
		 * Th DOMtable data
		 * @param $table
		 */
		this.setOriginalThDOM = $table => {
			const _thList = [];
			const _thDOM = $('thead[grid-manager-thead] th', $table);

			$.each(_thDOM, (i, v) => {
				_thList.push(v.getAttribute('th-name'));
			});
			store.originalTh[Base.getKey($table)] = _thList;
			$table.data('originalThList', _thList);
		};

		/**
		 * Th DOMtable data
		 * @param $table
		 * @returns {*|HTMLElement|jTool}
		 */
		this.getOriginalThDOM = $table => {
			const _thArray = [];
			const _thList = store.originalTh[Base.getKey($table)];
			$.each(_thList, (i, v) => {
				_thArray.push($(`thead[grid-manager-thead] th[th-name="${v}"]`, $table).get(0));
			});
			return $(_thArray);
		};

		/**
		 * GM
		 * @param $table
		 */
		this.__setGridManager = ($table, GM) => {
			store.gridManager[Base.getKey($table)] = GM;
		};

		/**
		 * GM
		 * @param $table
		 * @returns {*}
		 * @private
		 */
		this.__getGridManager = $table => {
			if (!$table || $table.length === 0) {
				return {};
			}
			const settings = this.getSettings($table);
			const gridManager = store.gridManager[Base.getKey($table)] || {};

			$.extend(gridManager, settings);
			return gridManager;
		};
	}
}
export default new Cache();



